* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.state {
  max-width: 640px;
  width: calc(100% - 20px);
  margin: 0 auto;
  border: 2px solid #333;
  padding: 30px;
  background-color: rgb(255, 229, 189);
}

.state h1 {
  text-align: center;
  color: tomato;
  margin-bottom: 10px;
}

.state img {
  max-width: 580px;
  width: 100%;
}

.state input {
  width: 520px;
  height: 30px;
}

.state button {
  margin-left: 10px;
  width: 45px;
  height: 30px;
}